<template><div class="markdown-body"><h1>文本输入框</h1>
<p><code v-pre="">mu-text-field</code> 用于收集用户提供的信息。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-text-field&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field placeholder=\&quot;Please input......\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field disabled placeholder=\&quot;disabled input\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field placeholder=\&quot;error input\&quot; error-text=\&quot;Please input......\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field label=\&quot;Email Address\&quot; suffix=\&quot;@gmail.com\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field label=\&quot;Label Float\&quot; prefix=\&quot;$\&quot; label-float&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field label=\&quot;Password\&quot; :action-icon=\&quot;visibility ? 'visibility_off' : 'visibility'\&quot; :action-click=\&quot;() =&gt; (visibility = !visibility)\&quot; :type=\&quot;visibility ? 'text' : 'password'\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field label=\&quot;Full Width\&quot; full-width&gt;&lt;/mu-text-field&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      visibility: false\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-text-field></mu-text-field><br>
  <mu-text-field placeholder="Please input......"></mu-text-field><br>
  <mu-text-field disabled placeholder="disabled input"></mu-text-field><br>
  <mu-text-field placeholder="error input" error-text="Please input......"></mu-text-field><br>
  <mu-text-field label="Email Address" suffix="@gmail.com"></mu-text-field><br>
  <mu-text-field label="Label Float" prefix="$" label-float=""></mu-text-field><br>
  <mu-text-field label="Password" :action-icon="visibility ? 'visibility_off' : 'visibility'" :action-click="() => (visibility = !visibility)" :type="visibility ? 'text' : 'password'"></mu-text-field><br>
  <mu-text-field label="Full Width" full-width=""></mu-text-field>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Please input......"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"disabled input"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"error input"</span> <span class="hljs-attr">error-text</span>=<span class="hljs-string">"Please input......"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Email Address"</span> <span class="hljs-attr">suffix</span>=<span class="hljs-string">"@gmail.com"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Label Float"</span> <span class="hljs-attr">prefix</span>=<span class="hljs-string">"$"</span> <span class="hljs-attr">label-float</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Password"</span> <span class="hljs-attr">:action-icon</span>=<span class="hljs-string">"visibility ? 'visibility_off' : 'visibility'"</span> <span class="hljs-attr">:action-click</span>=<span class="hljs-string">"() =&gt; (visibility = !visibility)"</span> <span class="hljs-attr">:type</span>=<span class="hljs-string">"visibility ? 'text' : 'password'"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Full Width"</span> <span class="hljs-attr">full-width</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">visibility</span>: <span class="hljs-literal">false</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="duo-xing-shu-ru"><a class="header-anchor" href="#duo-xing-shu-ru" aria-hidden="true">¶</a> 多行输入</h2>
<p>设置 <code v-pre="">multi-line</code> 之后会被渲染成 <code v-pre="">textarea</code> 标签，通过 <code v-pre="">rows</code> 和 <code v-pre="">rowsMax</code> 控制显示输入框的行数。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-text-field placeholder=\&quot;默认3行, 最大显示6行\&quot; multi-line :rows=\&quot;3\&quot; :rows-max=\&quot;6\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field multi-line=\&quot;\&quot; :rows=\&quot;4\&quot; full-width&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-text-field placeholder="默认3行, 最大显示6行" multi-line="" :rows="3" :rows-max="6"></mu-text-field><br>
  <mu-text-field multi-line="" :rows="4" full-width=""></mu-text-field><br>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"默认3行, 最大显示6行"</span> <span class="hljs-attr">multi-line</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">:rows-max</span>=<span class="hljs-string">"6"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">multi-line</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">full-width</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="tu-biao-xian-shi"><a class="header-anchor" href="#tu-biao-xian-shi" aria-hidden="true">¶</a> 图标显示</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-text-field type=\&quot;number\&quot; placeholder=\&quot;电话号码\&quot; icon=\&quot;phone\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field multi-line :rows=\&quot;4\&quot; icon=\&quot;comment\&quot; placeholder=\&quot;输入信息\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-text-field type="number" placeholder="电话号码" icon="phone"></mu-text-field><br>
  <mu-text-field multi-line="" :rows="4" icon="comment" placeholder="输入信息"></mu-text-field><br>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"number"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"电话号码"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"phone"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">multi-line</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"comment"</span>  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"输入信息"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="bang-zhu-wen-zi-he-cuo-wu-zhuang-tai"><a class="header-anchor" href="#bang-zhu-wen-zi-he-cuo-wu-zhuang-tai" aria-hidden="true">¶</a> 帮助文字和错误状态</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-text-field label=\&quot;UserName\&quot; label-float help-text=\&quot;用户名为6-12长度的字符\&quot; icon=\&quot;account_circle\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field label=\&quot;Password\&quot; label-float error-text=\&quot;请输入密码\&quot; icon=\&quot;locked\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-text-field label="UserName" label-float="" help-text="用户名为6-12长度的字符" icon="account_circle"></mu-text-field><br>
  <mu-text-field label="Password" label-float="" error-text="请输入密码" icon="locked"></mu-text-field><br>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"UserName"</span> <span class="hljs-attr">label-float</span> <span class="hljs-attr">help-text</span>=<span class="hljs-string">"用户名为6-12长度的字符"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"account_circle"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Password"</span> <span class="hljs-attr">label-float</span> <span class="hljs-attr">error-text</span>=<span class="hljs-string">"请输入密码"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"locked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="shu-ru-chang-du-xian-zhi"><a class="header-anchor" href="#shu-ru-chang-du-xian-zhi" aria-hidden="true">¶</a> 输入长度限制</h2>
<p>设置 <code v-pre="">max-length</code> 后会启动输入长度限制，并显示输入字符数。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-text-field placeholder=\&quot;最多不超过10个字符\&quot; :max-length=\&quot;10\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n  &lt;mu-text-field placeholder=\&quot;不允许超过100个字符\&quot; multi-line :rows=\&quot;3\&quot; :max-length=\&quot;100\&quot;&gt;&lt;/mu-text-field&gt;&lt;br&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-text-field placeholder="最多不超过10个字符" :max-length="10"></mu-text-field><br>
  <mu-text-field placeholder="不允许超过100个字符" multi-line="" :rows="3" :max-length="100"></mu-text-field><br>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最多不超过10个字符"</span> <span class="hljs-attr">:max-length</span>=<span class="hljs-string">"10"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"不允许超过100个字符"</span> <span class="hljs-attr">multi-line</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">:max-length</span>=<span class="hljs-string">"100"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>输入框颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>icon</td>
<td>输入框左边的图标</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>标签文本</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label-float</td>
<td>标签是否浮动</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>underline-show</td>
<td>是否显示底部的横线</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>error-text</td>
<td>错误提醒文字，如果此参数有值，那么输入框会转为错误的状态, 显示效果将会更改</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>help-text</td>
<td>帮助文字</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>full-width</td>
<td>是否将宽度设置为 100%, 默认 256px</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>输入框是否不可用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>multi-line</td>
<td>是否多行输入</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>max-length</td>
<td>最多可输入字符数</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>rows</td>
<td>行数</td>
<td>Number</td>
<td>—</td>
<td>1</td>
</tr>
<tr>
<td>rows-max</td>
<td>最大行数</td>
<td>—</td>
<td>—</td>
<td></td>
</tr>
<tr>
<td>action-icon</td>
<td>输入框右边的图标</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>action-click</td>
<td>图标点击之后执行的函数</td>
<td>Function</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>solo</td>
<td>改变样式，设置之后会隐藏输入框的下划线和标签</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>prefix</td>
<td>输入框前缀内容</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>suffix</td>
<td>输入框尾部内容</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      text: '',
      visibility: false
    }
  }
}
</script>


